.App {
  text-align: center;
}
h1 {
  color: rgb(0, 160, 209);
  font-size: 16pt;
  font-weight: 400;
  & + p {
    font-size: 13pt;
    color: rgb(151, 134, 121);
  }
}

.tabs {
  width: 100%;
  position: relative;
  button {
    border: none;
    width: 50%;
    position: relative;
    z-index: 1;
    background: none;
    transition: color 0.2s ease-in 0.2s;
    &.active {
      color: white;
    }
  }
  &:after {
    content: '';
    position: absolute;
    width: 50%;
    top: 0;
    left: 0;
    height: 100%;
    background: rgb(0, 160, 209);
    border-radius: 20px;
    transition: left 0.3s ease-in-out;
  }
}

$start: 0;
$end: 1;

@for $i from $start through $end {
  .current-tab--#{$i} {
    &:after {
      left : #{$i * 100% / ($end + 1)};
    }
  }
}
